<template>
    <div class="container">
        <div class="top">
            <div v-for="(list,index) in putlist" :key="list[index]" class="left">
                <draggable :list="list" :group="{name:'cards',put: putlist[index].length == 0 }">
                    <div
                        v-for="card in list"
                        :key="card"
                        class="list-item"
                    >
                        <img
                            :src="require(`@/assets/${getSuit(card)}${getNum(card)}.png`)"
                            alt
                        />
                    </div>
                </draggable>
            </div>
            <div v-for="(list,index) in endlist" :key="list[index]" class="right">
                <draggable :list="list" :group="{name:'cards',put:endlist[index]}">
                    <div
                        v-for="card in list"
                        :key="card"
                        class="list-item"
                    >
                        <img
                            :src="require(`@/assets/${getSuit(card)}${getNum(card)}.png`)"
                            alt
                        />
                    </div>
                </draggable>
            </div>
        </div>
        <div class="down">
            <div v-for="(list,index) in lists" :key="list[index]">
                <draggable :list="list" :group="{name:'cards'}" draggable=".lastItem" :move="checkmove">
                    <div
                        v-for="(card, cardIdx) in list"
                        :key="cardIdx"
                        class="list-item"
                        :class="{ lastItem: cardIdx === list.length - 1 }"
                    >
                        <img
                            :src="require(`@/assets/${getSuit(card)}${getNum(card)}.png`)"
                            alt
                        />
                    </div>
                </draggable>
            </div>
        </div>
    </div>
</template>
<script>
import draggable from 'vuedraggable'
export default {
    data() {
        return {
            lists:[],
            putlist:[[],[],[],[]],
            endlist:[[],[],[],[]]
        }
    },
    components:{
        draggable
    },
    computed:{
    randomCards() {
            let arr = [];
            let json = {};
            while (arr.length < 52) {
                let k = Math.floor(Math.random() * 52) + 1;
                if (!json[k]) {
                    json[k] = true;
                    arr.push(k);
                }
            }
            return arr;
    }
    },
    methods:{
        checkmove(event){
            console.log(123)
            return 
        },
        getNum(id) {
            return id % 13 === 0 ? 13 : id % 13;
        },
        // 取得牌的花色
        getSuit(id) {
            if (id <= 13) {
                return '方塊';
            }
            else if (id >= 14 && id <= 26) {
                return '梅花';
            }
            else if (id >= 27 && id <= 39) {
                return '黑桃';
            }
            else {
                return '愛心';
            }
        },
        generateLists() {
            let start = 0;
            let end = start + 7;
            for (let i = 0; i < 8; i++) {
                this.$set(this.lists, i, this.randomCards.slice(start, end));
                start = end;
                end += i < 3 ? 7 : 6;
            }
            console.log(this.lists)
        },
    },
    created(){
        this.generateLists()
    }
}
</script>
<style>
.down{
    padding-top: 100px;
}
.down .list-item{
    margin-top: -106px;
}
.left > div,.right > div{
    position: relative;
    min-height: 145px;
    width: 110px;
    border: 2px solid #ededed;
    border-radius: 10px;
    background-position: 50%;
    background-repeat: no-repeat;
}
.top{
    display:flex;
    justify-content:space-around;
}
.left {
    margin-bottom: 20px;
}
.left .list-item{
    position: absolute;
}
.container {
    padding-top: 30px;
    padding-bottom: 82px;
}
.down{
    display: flex;
     justify-content:space-around;
}
</style>
